<template>
    <el-table :data="this.$store.state.data" fit show-header empty-text="暂无文件" style="width: 100%" max-height="830px" 
    @cell-click="showrow"
    @cell-mouse-enter="MouseEnter"
    @cell-mouse-leave="MouseLeave"
    >
      <el-table-column type="selection" width="30" class="checkbox" >
      </el-table-column>
      <el-table-column prop="name" label="文件名" >
        <template slot-scope="scope">
        <i class="fa fa-folder fa-2x" aria-hidden="true" style="color: #FFD659" v-if="scope.row.type=='folder'&&!scope.row.edit"></i>
        <i class="fa fa-picture-o fa-2x" aria-hidden="true" style="color: #FF7743" v-if="scope.row.type=='image'&&!scope.row.edit"></i>
        <i class="fa fa-file-word-o fa-2x" aria-hidden="true" style="color: #4D97FF" v-if="scope.row.type=='document'&&!scope.row.edit"></i>
        <i class="fa fa-file-video-o fa-2x" aria-hidden="true" style="color: #8183F1" v-if="scope.row.type=='video'&&!scope.row.edit"></i> 
        <template v-if="scope.row.edit">
          <el-input class="edit-input" size="small" v-model="scope.row.name"></el-input>
        </template>
        <span id="filename" v-else>{{ scope.row.name }}</span>
        <!-- <i class="fa fa-share-alt fa-2x" aria-hidden="true" style="color: #4D97FF"></i> -->
      </template>
      </el-table-column>
      <el-table-column >
      <template slot-scope="scope" >
        <el-button v-show="scope.row.edit" type="success" circle @click="handleSave(scope.$index, scope.row)" size="small" icon="el-icon-check"></el-button>
        <el-button v-show="scope.row.edit" type="danger" circle @click="handleCancel(scope.$index, scope.row)" size="small" icon="el-icon-close"></el-button>
        <el-button type="primary" circle v-show="!scope.row.edit&&scope.row.hover" @click="handleEdit(scope.$index, scope.row)" size="small" icon="el-icon-edit"></el-button>
      </template>
    </el-table-column>
      <el-table-column prop="size" label="大小" >
      </el-table-column>
      <el-table-column prop="date" label="修改时间" >
      </el-table-column>
    </el-table>
</template>

<script>

export default {
    data() {
        return {
          show: false,
          editClick: false,
        }
      },
    props: {

    },
    methods: {
      showrow(row,column) {
        if(column.label=='文件名'&&this.$store.state.allowClick==true){
          if(row.type == 'folder'){
          this.$store.commit("folderClick",row.name);
          this.$store.state.fileposition.push(row.name);
          console.log('发送请求');
          this.$axios({
                url:'http://192.168.0.234:9527/cd/file/getFiles/'+row.id,
                method:'get',
                // params: row.id
            }).then(status=>{
              console.log(status)
            })
        }
        }
      },
      MouseEnter(row) {
        if(this.$store.state.allowHover==true){
          row.hover = true;
        }
      },
      MouseLeave(row) {
        row.hover = false;
      },
      handleEdit(index,row){
        this.$store.state.Formername = row.name;
        this.editClick = true;
        row.edit = !row.edit;
        this.$store.state.allowClick = false;
        this.$store.state.allowHover = false;
      },
      handleSave(index,row){
        if(row.name==''){
          console.log(this.$store.state.Formername)
          row.name = this.$store.state.Formername;
          this.$message({
            message: '文件名不能为空',
            type: 'success'
        })
        }
        else {
            this.$message({
            message: '文件名已经成功修改',
            type: 'success'
        })
        }
        row.edit = false;
        this.editClick = false;
        this.$store.state.allowClick = true;
        this.$store.state.allowHover = true;
      },
      handleCancel(index,row){
        row.edit = false;
        this.$store.state.allowClick = true;
        this.$store.state.allowHover = true;        
        if(this.editClick) {
          row.name = this.$store.state.Formername;
          this.$message({
            message: '重命名已取消',
            type: 'success'
          })
          this.editClick = false;
        }
        else {
          this.$store.state.data.pop();
        }
        
      },
    }
}
</script>

<style lang="less">
 #filename {
     position: relative;
     bottom: 4px;
     margin-left: 15px;
     cursor: pointer;
 }
.fa-share-alt:before {
  margin-left: 150px;
}
.el-table .cell {
  text-overflow: clip!important
}
.el-checkbox__inner {
  border: 1px solid #DCDFE6!important /* no */
}
</style>